import React from 'react';

const SearchBox = ({ searchField, searchChange }) => {
	return (
		<div className='pa2'>
			<input 
			className='pa3 ba b--light-green bg-light-green grow shadow-5'
			type='search' 
			placeholder='Search Robots'
			onChange={searchChange} 
			/>
		</div>
	);
}

export default SearchBox;